.main{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .texto{
    display: flex;
    width: 90%;
    justify-content: center;
    margin-bottom: 20px;

    span{
      width: 5px;
      background-color: orange;
      margin: 0 15px;
      height: 120px;
    }
    div{
      width: 200px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #666666;

      &.numero{
        font-size: 4rem;
        justify-content: flex-end;
      }
      &.tex{
        flex-direction: column;
        p{
          margin: 2px;
        }
      }
    }
  }

  .img{
    max-width: 200px;
    animation: upAndDown 2s ease-in-out alternate infinite;
  }
  @keyframes upAndDown {
    0%{
      transform: translateY(-10px);
    }
    100%{
      transform: translateY(10px);
    }
  }
}
