.form_evento, .form_pedido{
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}
.tabla-productos{

  .prod{
    display: flex;
    width: 100%;
    justify-content: space-around;
    font-size: .8rem;
    border-bottom: 1px solid #6666;
    div{
      width: 20%;
      border-right: 1px solid #6666;
    }
    div:last-child{
      border-right: none;
    }
    &:last-child{
      border-bottom: none;
    }
    
    
    div.dec{
      width: 30%;
      word-break: break-all;
    }
    div.acciones{
      width: 10%;
    }
  }
  
}
.total{
  border: 1px solid #ff9800;
  margin-top: 20px;
  padding: 10px 0;
  color: black;
  font-size: 1.2rem;
}

