body{
  margin: 0;
  box-sizing: border-box;
  color: #666666;
  max-width: 450px;
  height: 100vh;
  margin: auto;
}

.App{
  height: 100vh;
}
.link-regreso{
  color: orange;
  position: absolute;
  top: 5px;
  left: 5px;
}
.titulo{
  margin-top: 0;
}

// Boton agregar nuevo
.btn_nuevo{
  text-decoration: none;
  background-color: #4caf50;
  border-radius: 5px;
  margin: 0 5px;
  img{
    width: 20px;
    padding-right: 5px;
  }
}

// Formularios
form{
  width: 90%;
  text-align: center;
  padding-bottom: 20px;
  overflow: auto;

  .field_group{
    display: flex;

    div{
      width: 50%;
      padding: 0 10px;
    }
  }
  .field{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;

    label{
      font-size: 1.3rem;
    }

    input, textarea{
      padding: 5px 10px;
      font-size: 1.1em;
      width: 100%;
      box-sizing: border-box;
      border-radius: 5px;
      border: 1px solid #6666;
    }
    

  }
  
  button{
    color: #fff;
    background-color: #4caf50;
    border: none;
    border-radius: 5px;
    padding: 5px 25px;
    font-size: 1.3rem;
    margin-top: 25px;
  }
  button.tiny{
    font-size: .9rem;
    margin-top: 10px;
  }
  select{
    color: #fff;
    background-color: #faa307;
    border: none;
    border-radius: 5px;
    padding: 2px 10px;
  }
  .division{
    width: 60%;
    height: 1px;
    background-color: #00b4d8;
    display: block;
    margin: 20px auto 30px;
  }
  .tres .field label{
    font-size: 14px;
  }
  
}

// Dise√±o de las esquinas
.esquina{
  position: relative;
  height: 100%;
  position: relative;

  &::after, &::before{
    content: "";
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    border: 50px solid transparent;
  }
  &::after{
    bottom: 0;
    left: 0;
  }
  &::before{
    top: 0;
    right: 0;
  }
  &.home{
    &::after{
      border-bottom-color: #e9c46a;
      border-left-color: #e9c46a;
    }
    &::before{
      border-top-color: #e9c46a;
      border-right-color: #e9c46a;
    }
  }
  &.form_evento{
    &::after{
      border-bottom-color: #e76f51;
      border-left-color: #e76f51;
    }
    &::before{
      border-top-color: #e76f51;
      border-right-color: #e76f51;
    }
  }
  &.evento{
    &::after{
      border-bottom-color: #2a9d8f;
      border-left-color: #2a9d8f;
    }
    &::before{
      border-top-color: #2a9d8f;
      border-right-color: #2a9d8f;
    }
  }
  &.form_pedido{
    &::after{
      border-bottom-color: #00b4d8;
      border-left-color: #00b4d8;
    }
    &::before{
      border-top-color: #00b4d8;
      border-right-color: #00b4d8;
    }
  }
  &.pedido{
    &::after{
      border-bottom-color: #560bad;
      border-left-color: #560bad;
    }
    &::before{
      border-top-color: #560bad;
      border-right-color: #560bad;
    }
  }
  &.login{
    &::after{
      border-bottom-color: #edffa9;
      border-left-color: #edffa9;
    }
    &::before{
      border-top-color: #edffa9;
      border-right-color: #edffa9;
    }
  }
  &.productos{
    &::after{
      border-bottom-color: #81b214;
      border-left-color: #81b214;
    }
    &::before{
      border-top-color: #81b214;
      border-right-color: #81b214;
    }
  }
}

// Loader
.contenedor-loader{
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
  background: #ff8474;
  flex-direction: column;
  color: #fff;
  font-size: 2.3rem;
}
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #00adb5;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

// Select General

.select_default{
  color: #fff;
  background-color: #009688;
  border: none;
  border-radius: 5px;
  padding: 2px 10px;
}