.tabla{
  width: 100%;
  border-collapse: collapse;

  tbody{
    tr{
      border-bottom: 1px solid #666666;
    }
    td{
      padding: 10px 0;
    }
  }
  .hamburguesa{
    position: relative;
    &.on .menu{
      display: block;
    }
    .menu{
      display: none;
      box-shadow: 0 0 10px -5px #666666;
      border-radius: 5px;
      padding: 1px 5px;
      position: absolute;
      width: 110px;
      right: 10px;
      background: #fff;
      z-index: 10;
    }
    .menu ul{
      list-style: none;
      padding: 0;
      text-align: initial;
    }
    .menu li{
      margin: 10px 0;
    }
    .btn_acciones{
      padding: 0 10px;
      background-color: transparent;
      border: none;
      display: inline-block;
      img{
        width: 16px;
      }
    }
    .mostrar .btn_acciones{
      text-decoration: none;
      color: #2196f3;
      img{
        padding-right: 10px;
      }
    }
    .editar{
      color: #2196f3;
    }
    .eliminar{
      color: #f44336;
    }
  }
  .no-pagado, .cerrado{
    color: #f44336;
  }
  .abierto, .entregado{
    color: #4caf50;
  }
  .pendiente{
    color: #00bcd4;
  }
}