.modal{
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  justify-content: center;
  display: none;
  align-items: center;
  background: rgba(255, 193, 7, .5);
  z-index: 10;
  &.show{
    display: flex;
  }

  .centro{
    width: 200px;
    background-color: #fff;
    padding: 50px 30px;
    border-radius: 10px;
    text-align: center;

    .icono{
      margin-top: 25px;
      img{
        max-width: 32px;
      }
    }

    .titulo{
      font-size: 1.3rem;
      margin: 10px 0;
    }
    .texto{
      font-size: 1rem;
    }
    .btns{
      display: flex;
      justify-content: space-evenly;

      button{
        border: none;
        border-radius: 5px;
        padding: 5px 15px;
        color: #fff;
        margin-top: 30px;
      }
      .aceptar{
        background: #4caf50;
      }
      .cancelar{
        background: #f44336;
      }
    }
  }
}