.evento{
  text-align: center;
  overflow: hidden;

  .fecha-evento{
    color: #9e9e9e;
  }

  .buscador{
    display: flex;
    flex-direction: column;

    label{
      font-size: 1.5rem;
    }
    input{
      width: 90%;
      margin: 10px auto;
      border-radius: 10px;
      border: 1px solid #00bcd4;
      padding: 5px 15px;
      box-sizing: border-box;
      outline: none;
    }
  }
  .filtros{
    display: flex;
    justify-content: space-evenly;
    margin: 10px 0;
    
    div{
      display: flex;
      flex-direction: column;
    }
  }
  .lista-pedidos{
    overflow: auto;
    display: flex;
    height: 60%;
  }
}