.productos{
  width: 100%;
  margin-bottom: 50px;
  height: 100vh;
  text-align: center;
  
  .fecha_hora{
    display: flex;
    width: 70%;
    margin: 20px auto;
    justify-content: space-evenly;
    
  }
  .terminado, .pendiente{
    
    width: 60%;
    margin: 25px auto;
    padding: 10px;
    box-sizing: border-box;
    
  }
  .terminado{
    color: #f44336;
    border: 1px solid #f44336;
  }
  .pendiente{
    color: #2196f3;
    border: 1px solid #2196f3;
  }
}